.tabs{
    display: flex;
    justify-content: center;
    .tab{
        display: flex;
        align-items: center;
        justify-content: center;
        width: 33%;
        padding-top: 1.5rem;
        padding-bottom: 1.5rem;
        cursor: pointer;
        border: 1px solid $tab-border-color;
        @include pad{
            padding: 1rem 0;
        }
        @include iPhone{
            padding: .5rem 0;
        }
        &:hover{
            border-bottom: 8px solid $tab-active-color;
        }
        i{
            font-size: 3rem;
            margin-right: 0.5rem;
            @include pad{
                font-size: 2rem;
            }
            @include iPhone{
                font-size: 1rem;
            }
        }
        &-title{
            font-size: 2rem;
            @include pad{
                font-size: 1.5rem;
            }
            @include iPhone{
                font-size: .875rem;
            }
        }
    }
    .active{
        border-bottom: 8px solid $tab-active-color;
    }
}
.product-banner{
    display: flex;
    img{
        width: 50%;
        height: 304px;
        object-fit: cover;
        @include iPhone{
            height: 148px;
        }
    }
    .banner-sm{
        display: none;
        @include iPhone{
            display: block;
        }
    }
    .banner-lg{
        @include iPhone{
            display: none;
        } 
    }
}
.product-content{
    padding-top: 5rem;
    padding-bottom: 10rem;
    @include pad{
        padding-top: 3.5rem;
        padding-bottom: 6rem;
    }
    @include iPhone{
        padding-top: 2rem;
        padding-bottom: 3.5rem;
    }
    .title{
        font-size: 2.625em;
        @include pad{
            font-size: 2rem;
        }
        @include iPhone{
            font-size: 1.25rem;
        }
    }
    .subtitle{
        font-size: 2em;
        margin-bottom: 2.5rem;
        text-align: center;
        @include pad{
            font-size: 1.5rem;
            margin-bottom: 2rem;
        }
        @include iPhone{
            font-size: 1rem;
            margin-bottom: 1.25rem;
        }
    }
}
.product-list{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}
.product-item{
    width: 23%;
    margin-bottom: 6.5rem;
    @include pad{
        width: 47.8%;
        margin-bottom: 4.5rem;
    }
    @include iPhone{
        width: 100%;
        margin-bottom: 2rem;
    }
    img{
        width: 100%;
        height: 164px;
        object-fit: cover;
    }
    &-info{
        display: flex;
        align-items: center;
        justify-content: space-between;
        font-size: 1.5rem;
        @include iPhone{
            font-size: 1rem;
        }
        .price{
            color: $text-secondary;
        }
    }
    &-color{
        display: flex;
    }
    .color-box{
        @include size(24px, 24px);
        border-radius: 4px;
        cursor: pointer;
        @include iPhone{
            @include size(16px, 16px);
        }
        &:hover{
            border: 3px solid #000;
        }
    }
    .d-brown{
        background: #5F3E2D;
        margin-right: 0.5rem;
        @include iPhone{
            margin-right: 0.25rem;
        }
    }
    .l-brown{
        background: #B75929;
    }
}
.product-item-lg{
    @include pad{
        display: none;
    }
}
.product-item-md{
    @include iPhone{
        display: none;
    }
}